	module.exports = {"pathContext":{"docHtml":"<h1>path-parse <a href=\"https://travis-ci.org/jbgutierrez/path-parse\"><img src=\"https://travis-ci.org/jbgutierrez/path-parse.svg?branch=master\" alt=\"Build Status\"></a></h1>\n<blockquote>\n<p>Node.js <a href=\"https://nodejs.org/api/path.html#path_path_parse_pathstring\"><code>path.parse(pathString)</code></a> ponyfill.</p>\n</blockquote>\n<blockquote>\n<p>Ponyfill: A polyfill that doesn't overwrite the native method</p>\n</blockquote>\n<h2>Install</h2>\n<pre><code>$ npm install --save path-parse\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">var pathParse = require('path-parse');\n\npathParse('/home/user/dir/file.txt');\n//=> {\n//       root : \"/\",\n//       dir : \"/home/user/dir\",\n//       base : \"file.txt\",\n//       ext : \".txt\",\n//       name : \"file\"\n//   }\n</code></pre>\n<h2>API</h2>\n<p>See <a href=\"https://nodejs.org/api/path.html#path_path_parse_pathstring\"><code>path.parse(pathString)</code></a> docs.</p>\n<h3>pathParse(path)</h3>\n<h3>pathParse.posix(path)</h3>\n<p>The Posix specific version.</p>\n<h3>pathParse.win32(path)</h3>\n<p>The Windows specific version.</p>\n<h2>License</h2>\n<p>MIT © <a href=\"http://jbgutierrez.info\">Javier Blanco</a></p>","docPath":"argo-ci/node_modules/path-parse/readme","proj":"argo-ci"}}

